html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: block;
  position: relative;
}

.nav__link:hover, .nav .is-active {
  color: #00acdc;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
  grid-template-columns: repeat(1, auto);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, auto);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, auto);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 19px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  object-fit: contain;
  width: 160px;
  height: 160px;
  margin: 0 auto;
  display: block;
}

.card__title {
  font: inherit;
  color: #060b35;
  margin-block: 27px 4px;
  font-weight: 500;
}

.card:hover > .card__title {
  color: #34568b;
}

.card__subtitle {
  color: #616070;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-label {
  color: #616070;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: background-color .3s;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  flex-direction: row;
  width: fit-content;
  margin: 0;
  padding: 0;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  list-style: none;
  display: block;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
